class Vue {
  constructor (options) {
    // 1. é€šè¿‡å±žæ€§ä¿å­˜é€‰é¡¹çš„æ•°æ®
    this.$options = options || {}
    this.$data = options.data || {}
    this.$el = typeof options.el === 'string' ? document.querySelector(options.el) : options.el
    // 2.æŠŠdataä¸­çš„æˆå‘˜è½¬æ¢æˆgetterå’Œsetterï¼Œæ³¨å…¥åˆ°vueå®žä¾‹ä¸­
    this._proxyData(this.$data)
    // 3.è°ƒç”¨observerå¯¹è±¡ï¼Œç›‘å¬æ•°æ®çš„å˜åŒ–
    new Observer(this.$data)
    // 4.è°ƒç”¨compilerå¯¹è±¡ï¼Œè§£æžæŒ‡ä»¤å’Œæ’å€¼è¡¨è¾¾å¼
    new Compiler(this)
  }
  _proxyData (data) {
    // éåŽ†dataä¸­çš„æ‰€æœ‰å±žæ€§
    Object.keys(data).forEach(key => {
      // æŠŠdataçš„å±žæ€§æ³¨å…¥åˆ°vueå®žä¾‹ä¸­
      Object.defineProperty(this, key, {
        enumerable: true,
        configurable: true,
        get () {
          return data[key]
        },
        set (newValue) {
          if (newValue === data[key]) {
            return
          }
          data[key] = newValue
        }
      })
    })
  }
}